// Start Slider Gallery
.slider {
  width: 100%;
  margin: auto;
  padding: 3px;
  background-color: #fff;
  img {
    max-width: 100%;
    border: 1px solid #ccc;
  }
  .master-img {
    min-height: 70vh;
    max-height: 80vh;
    width: 100%;
    display: block;
    // overflow: hidden;
    background-image: url(../images/pharmacy/2.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    // margin-bottom: 20px;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      padding: 10px;
      margin: 0 10px;
      width: 50px;
      height: 50px;
      z-index: 2;
      cursor: pointer;
      border: 2px solid #333;
      @include borderRadius(50%);
      background-size: cover;
      background-repeat: no-repeat;
    }
    span.left {
    }
    span.right {
    }
  }
  .thumbnails {
    display: grid;
    // grid-template-columns: repeat(auto-fill, minmax(20%, 1fr));
    // gap: 0px 0px;
    height: 200px;
    img {
      cursor: pointer;
      border: 3px solid #ccc;
      height: 200px;
      width: 100%;
      @include transitionTime(0.5s);
      @include opacity(0.7);
    }
    img:last-child {
      margin-right: 0 !important;
    }
    img.active {
      border: 3px solid #333;
      @include opacity(1);
    }
  }
}

